body {
  margin: 0;
}

.search-form {
  height: 50px;
  width: 100%;
  background-color: #007bff;
  justify-content: center;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
}

.search-form input[type="text"] {
  width: 300px;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 10px;
  font-size: 16px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 20px);
  border-radius: 5px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: translateY(-5px);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card .info {
  color: #333;
  background-color: #fff;
  padding: 10px;
}

.photo-card .info-item {
  margin: 0;
  padding: 5px 0;
}

.photo-card .info-item b {
  font-weight: bold;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  display: none;
}

